.cwp_list_wrapper {
  list-style-type: none;
  padding: 0px !important;
  margin: 0px !important; }

.cwp_list_wrapper li {
  display: inline-block; }

.cwp_list_wrapper li .acfb_tab_title {
  background: #f5f5f5;
  color: #000;
  padding: 10px 25px;
  margin-left: 5px;
  cursor: pointer; }
